import { Dispatcher } from "../redux/dispatcher/dispatcher";
import BackingState from "../redux/store/backingState";
import { BackingLayerData, BackingSignalProps, ChordBacking, VoicingProps } from "../redux/store/scoreData";
import { Store } from "../redux/store/store";
import { playPiano } from "./melodyUtil";
import { IntervalRelationProps, KEY12_SHARP_LIST, SymbolParams } from "./theoryUtils";



export namespace BackingUtil {

    // 
    export const OCTAVE_NUM = 8;
    // 
    export const STRUCT_NUM = 6;
    // 
    export const CHANNEL_MAX = 12;

    export const getInitialRecord = (divCount: number) => {
        return new Array<BackingState.NoteStatus | null>(divCount).fill(null);
    }

    export const getVoicingStructs = (table: boolean[][]): BackingState.ChannelParam[] => {
        const channels: BackingState.ChannelParam[] = [];
        for (let i = 0; i < table.length; i++) {
            for (let j = 0; j < table[i].length; j++) {
                if (table[i][j]) {
                    channels.push({ octave: i, structIndex: j });
                }
            }
        }
        if (channels.length === 0) {
            channels.push({ octave: -1, structIndex: 0 });
        }
        return channels;
    }

    export const getVoicingListFromTable = (table: boolean[][]) => {

        const voicingList: VoicingProps[] = [];
        // 
        if (table != null) {
            for (let i = 0; i < table.length; i++) {
                for (let j = 0; j < table[i].length; j++) {
                    if (table[i][j]) {
                        voicingList.push({ octave: i, struct: j });
                    }
                }
            }
        }
        return voicingList;
    }

    export const buildPatternProps = (editorPattern: BackingState.Pattern) => {
        const channelSize = editorPattern.channels.length;
        const layers: BackingLayerData[] = [];
        editorPattern.layers.forEach(layer => {
            const divs: BackingState.NoteDiv[] = layer.noteDivList;
            const signals: BackingSignalProps[] = [];
            layer.table.forEach((record, i) => {
                record.forEach((cell, j) => {
                    if (cell != null) {
                        signals.push({ x: j, y: i, status: cell });
                    }
                });
            });
            layers.push({ divs, notesList: signals });
        });
        return { channelSize, layers };
    }

    export const getChannels = (voicingStructs: BackingState.ChannelParam[], size: number) => {
        const channels: BackingState.ChannelParam[] = [];
        for (let i = 0; i < size; i++) {
            const channel: BackingState.ChannelParam = { octave: -1, structIndex: 0 };
            if (i < voicingStructs.length) {
                channel.octave = voicingStructs[i].octave;
                channel.structIndex = voicingStructs[i].structIndex;
            }
            channels.push(channel);
        }
        return channels.reverse();
    }

    export const convertEditorLayerFromScore = (channelSize: number, scoreLayers: BackingLayerData[]) => {

        const layers: BackingState.Layer[] = [];
        console.log(JSON.stringify(scoreLayers));
        scoreLayers.forEach(scoreLayer => {
            const table: (BackingState.NoteStatus | null)[][] = [];
            for (let i = 0; i < channelSize; i++) {
                const record: (BackingState.NoteStatus | null)[] = [];
                for (let j = 0; j < scoreLayer.divs.length; j++) {
                    const notes = scoreLayer.notesList.find(notes => notes.x === j && notes.y === i);
                    record.push(notes == null ? null : notes.status);
                }
                table.push(record);
            }
            layers.push({
                noteDivList: scoreLayer.divs,
                table,
                velocityList: []
            });
        });
        console.log(JSON.stringify(layers));
        return layers;
    }

    export const getPitchListFromVoicing = (voicingList: VoicingProps[], rootIndex: number, symbol: SymbolParams) => {
        return voicingList.map((item) => {
            const interval = IntervalRelationProps[symbol.structs[item.struct]];
            return 12 * item.octave + rootIndex + interval;
        });
    }

    export const playBacking = (store: Store, reserveTasks: NodeJS.Timeout[], bpm: number, backing: ChordBacking, pitchIndexList: number[], chordTime: number) => {
        if (backing.pattern == null) {
            pitchIndexList.forEach((index, j) => {
                const soundName = KEY12_SHARP_LIST[index % 12];
                const octave = Math.floor(index / 12);
                const soundFullName = soundName + octave;
                playPiano(soundFullName, store, chordTime);
            });
        } else {
            const pattern = backing.pattern;
            pattern.layers.forEach(layer => {
                const startTimeList: number[] = [];
                let total = 0;
                layer.divs.forEach(div => {
                    startTimeList.push(60 / bpm * total);
                    let normal = 4 / div.rate;
                    if (div.isDot) normal *= 1.5;
                    else if (div.div3 > 0) normal /= 3 * div.div3;
                    total += normal;
                });
                layer.notesList.forEach(notes => {

                    const startTime = startTimeList[notes.x];
                    const sustainTime = chordTime - startTime;
                    reserveTasks.push(
                        setTimeout(() => {
                            const index = pitchIndexList[notes.y];
                            const soundName = KEY12_SHARP_LIST[index % 12];
                            const octave = Math.floor(index / 12);
                            const soundFullName = soundName + octave;
                            playPiano(soundFullName, store, sustainTime);
                        }, startTime * 1000)
                    );
                });
            });

        }
    }

    export const stopPreview = (store:Store, state: BackingState.Editor, dispatcher: Dispatcher, reserveTasks: NodeJS.Timeout[]) => {
        store.instruments.piano?.stop();
        state.isPreview = false;
        reserveTasks.forEach(id => clearInterval(id));
        dispatcher.backing.setState(state);
    }
}

export default BackingUtil;